$primary_color: #e53238;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

#root {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.App {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffc0;
  font-size: 2rem;
}
